@-webkit-keyframes JoinLetter{
  0%{
    opacity:0;
    letter-spacing:1.6rem
  }

  100%{
    opacity:1;
    letter-spacing:0.2rem
  }
}

@keyframes JoinLetter{
  0%{
    opacity:0;
    letter-spacing:1.6rem
  }

  100%{
    opacity:1;
    letter-spacing:0.2rem
  }
}

.home-layout{
  height:100vh;
  flex-direction:column;
  display:flex;
  align-items:center;
  justify-content:center;
  background:linear-gradient(270deg, #0388cc, #00a9ff);
  background-size:400% 400%;
  -webkit-animation:GradientAnimation 5s ease infinite;
  animation:GradientAnimation 5s ease infinite
}

.home-layout img{
  margin-top:-1.25rem;
  width:300px;
  min-height:210px;
  -webkit-filter:grayscale(1) brightness(10);
  filter:grayscale(1) brightness(10)
}

.home-layout h1{
  font-size:2rem;
  font-weight:100;
  color:#fff
}

.home-layout p{
  font-size:1.5rem;
  animation:pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  font-weight:200;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

.home-layout .sw-con{
  width:250px;
  margin-top:0.75rem;
  margin-bottom:1.25rem;
  overflow:hidden;
  border-radius:0.375rem;
  --tw-bg-opacity:1;
  background-color:rgba(255, 255, 255, var(--tw-bg-opacity));
  --tw-bg-opacity:0.05
}

.home-layout .sw-con .swiper-slide{
  font-size:1.25rem;
  line-height:1.75rem;
  font-weight:300;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity));
  display:flex;
  align-items:center;
  justify-content:center
}

.home-layout .sw-con .swiper-vertical{
  height:50px
}

